import { Link, useNavigate } from "react-router-dom";
import React, { useEffect, useState } from "react";
// import { LoginApi } from "../../../api/user/login";
// import { useNavigate } from "react-router";
import { useDispatch, useSelector } from "react-redux";
import { setRefreshToken } from "../../../storage/Cookie";
import { SET_TOKEN } from "../../../store/Auth";
import axios from "axios";

import "./User.scss";
import logo from "../../../assets/images/logos/logo-border.png";
import naver from "../../../assets/images/icons/social-naver.png";
import kakao from "../../../assets/images/icons/social-kakao.png";
import google from "../../../assets/images/icons/social-google.png";
import GoogleLogIn from "./GoogleLogin";

// const testImage = 'https://cdn.visitkorea.or.kr/img/call?cmd=VIEW&id=552b48fc-ce4a-43dc-adf2-1f854e4abd8f&mode=progress';

const Login = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const [inputs, setInputs] = useState({
    email: "",
    password: "",
  });
  const { email, password } = inputs;
  const onChange = (e) => {
    const { name, value } = e.target;
    setInputs({
      ...inputs,
      [name]: value,
    });
    // console.log(inputs);
  };

  //  1.   (   )
  const isValidEmail = email.includes("@") && email.includes(".");
  //  2.     
  const isValidInput = email.length >= 1 && password.length >= 1;
  //   
  const getIsActive = isValidEmail && isValidInput === true;

  const handleSubmit = () => {
    console.log(".", inputs);
    if (!isValidEmail) {
      alert("   .");
    } else if (!isValidInput) {
      alert("   .");
    }
    if (getIsActive) {
      axios({
        url: "https://j7c103.p.ssafy.io:8080/api/auth/user/login",
        method: "post",
        data: inputs,
      })
        .then((res) => {
          const response = res.data.data;
          alert(res.data.message);
          const accessToken = response.accessToken;
          const refreshToken = response.refreshToken;
          console.log("access", accessToken);
          console.log("refresh", refreshToken);
          setRefreshToken(refreshToken);
          dispatch(
            SET_TOKEN({
              accessToken: accessToken,
              email: response.email,
              age: response.age,
              gender: response.gender,
              nickname: response.nickname,
              userId: response.id,
              pictureUrl: response.profile_path,
            })
          );
          navigate("/");
        })
        .catch((err) => {
          console.log(err);
          alert(err.response.data.message);
        });
    }
  };
  const onKeyDown = (e) => {
    if (e.key === "Enter") {
      handleSubmit();
    }
  };

  return (
    <div className="user-box">
      <div className="login-title">
        <img className="login-logo" src={logo} alt="" />
        <div className="title-box">
          <p className="title-top"></p>
          <p className="title-mid"> </p>
        </div>
      </div>
      <div className="login-page">
        <div className="login-page-name">
          <p></p>
        </div>
        <div className="login-inputs">
          <div className="login-input">
            <p className="login-input-name"></p>
            <input
              className="login-input-text"
              required
              autoFocus
              type="email"
              placeholder=" "
              onChange={onChange}
              name="email"
            />
          </div>
          <div className="login-input">
            <p className="login-input-name"></p>
            <input
              className="login-input-text"
              required
              type="password"
              placeholder=" "
              onChange={onChange}
              name="password"
              onKeyDown={onKeyDown}
            />
          </div>
          <button className="login-button" onClick={handleSubmit}>
            Login
          </button>
        </div>
        <div className="signup-link">
          <p className="signup-text">
            ?{" "}
            <Link className="signup-btn" to="/Signup">
              
            </Link>
            
          </p>
        </div>
      </div>
      {/* <a href="https://i7c103.p.ssafy.io:8080/oauth2/authorization/google?redirect_uri=https://i7c103.p.ssafy.io:443/oauth/redirect">
        
      </a> */}
      {/* <p className="social-title"></p>
      <div className="social-btns">
        <GoogleLogIn />
      </div> */}
      <div className="bottomBack"></div>
    </div>
  );
};
export default Login;
