// import { Link } from 'react-router-dom';
import "./AdminModule.scss";
import { userDelete } from "../../api/user/userDelete";
// import { logout } from "../../api/user/logout";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router";
import { useEffect } from "react";
import axios from "axios";
import { getCookieToken, removeCookieToken } from "../../storage/Cookie";
import { DELETE_TOKEN } from "../../store/Auth";

const AdminModule = () => {
  // store  Access Token   
  const accessToken = useSelector((state) => state.authToken.accessToken);
  const userId = useSelector((state) => state.authToken.userId);
  const refreshToken = getCookieToken();

  const dispatch = useDispatch();
  const navigate = useNavigate();

  function userDeleteClick() {
    // const conf = confirm('?');
    if (!!window.confirm("?")) {
      axios({
        url: `https://j7c103.p.ssafy.io:8080/api/user/${userId}`,
        method: "delete",
        data: userId,
        headers: {
          "X-AUTH-TOKEN": accessToken,
        },
      })
        .then((res) => {
          dispatch(DELETE_TOKEN()); // store    
          removeCookieToken(); // cookie  refresh token 
          const response = res.data;
          alert("  .");
          console.log(response);
          navigate("/"); //  
        })
        .catch((err) => {
          console.log(err);
          alert(err);
        });
    } else {
      alert("    .");
    }
  }

  function logOutClick() {
    console.log("");
    axios({
      url: "https://j7c103.p.ssafy.io:8080/api/logout",
      method: "get",
      headers: {
        "REFRESH-TOKEN": refreshToken,
      },
    })
      .then((res) => {
        dispatch(DELETE_TOKEN()); // store    
        removeCookieToken(); // cookie  refresh token 
        const response = res.data;
        alert(response.message); //   
        console.log(response.data);
        navigate("/"); //  
      })
      .catch((err) => {
        console.log("logout err", err);
        alert(err);
      });
  }

  return (
    <div>
      <div className="box">
        <p className="boxIn" onClick={logOutClick}>
          
        </p>
      </div>
    </div>
  );
};

export default AdminModule;
