import React, { useEffect, useState } from "react";
import luggage from '../../assets/images/luggage.png';
import './BoddariSave.scss';
import {useLocation, Link, useNavigate} from 'react-router-dom';
import axios from "axios";
import { useSelector } from "react-redux";

function BoddariSave() {
  const navigate = useNavigate();
  const location = useLocation();
  const attraction = location.state.attraction;
  const [name, setName] = useState("");
  const [memo, setMemo] = useState("");
  const accessToken = useSelector((state) => state.authToken.accessToken);
  const userId = useSelector((state) => state.authToken.userId);

  const onChangeName = (e) => {
    setName(e.target.value);
  };
  const onChangeMemo = (e) => {
    setMemo(e.target.value);
  };

  const save = async () => {
    try {
      const response = await axios({
         url: `https://j7c103.p.ssafy.io:8080/api/bag/create/${userId}`,
         method: 'post',
         headers: {
           'X-AUTH-TOKEN' : accessToken,
         },
         data: {
           attractionId: attraction,
           memo: memo,
           name: name,
         }
        })
      // console.log(response.data.data.split(':',2)[1]);
      const bagId = response.data.data.split(':',2)[1]
      alert('  ')
      navigate(`/Betail/${bagId}`)
    } catch (err) {
      console.log("save", err);
    }
  };
  return (
    <div className="BoddariSave">
      <div className="BoddariSave-top">
        <img src={luggage} alt="image" />
      </div>
      <div className="BoddariSave-input">
        <label>
           
          <input
            id="Boddari-name"
            type="text"
            placeholder="  ."
            onChange={onChangeName}
          />
        </label>
        <label>
          
          <input
            id="Boddari-memo"
            type="text"
            placeholder=" ."
            name="boddariMemo"
            onChange={onChangeMemo}
          />
        </label>
      </div>
      <div className="BoddariSave-btn">
        <button
          onClick={() => {
            save();
          }}
        >
          
        </button>
        <Link to="/">
          <button></button>
        </Link>
      </div>
    </div>
  );
}

export default BoddariSave;