import "./User.scss";
import { signUp } from "../../../api/user/signup";

import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import logo from "../../../assets/images/logos/logo-border.png";
import naver from "../../../assets/images/icons/social-naver.png";
import kakao from "../../../assets/images/icons/social-kakao.png";
import google from "../../../assets/images/icons/social-google.png";
import GoogleLogIn from "./GoogleLogin";

import { useDispatch, useSelector } from "react-redux";
import { setRefreshToken } from "../../../storage/Cookie";
import { SET_TOKEN } from "../../../store/Auth";
import axios from "axios";

// mui (radio btn)
// import * as React from "react";
import Radio from "@mui/material/Radio";
import RadioGroup from "@mui/material/RadioGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormControl from "@mui/material/FormControl";
import FormLabel from "@mui/material/FormLabel";

const Signup = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const [inputs, setInputs] = useState({
    email: "",
    password: "",
    nickname: "",
    age: "",
    gender: "",
  });
  const [rePasswordInput, setRePasswordInput] = useState({
    re_password: "",
  });
  const { email, password, nickname, age, gender } = inputs;
  const { re_password } = rePasswordInput;
  const onChange = (e) => {
    const { name, value } = e.target;
    setInputs({
      ...inputs,
      [name]: value,
    });
    console.log(inputs);
  };
  const onChangeRePassword = (e) => {
    setRePasswordInput({
      re_password: e.target.value,
    });
  };

  //  1.  : '@', '.'   .
  const isValidEmail = email.includes("@") && email.includes(".");
  //  2.     .
  const specialLetter = password.search(/[`~!@@#$%^&*|'";:/?]/gi);
  //  2.  1 ,  8 .
  const isValidPassword = password.length >= 8 && specialLetter >= 1;
  //  3.  input value 1   
  const isValidInput =
    email.length >= 1 && password.length >= 1 && re_password.length >= 1;

  const handleSubmit = (e) => {
    e.preventDefault();

    //  ,    
    const re_pass = password === re_password;

    //      true  getIsActive .      .
    const getIsActive =
      isValidEmail && isValidPassword && isValidInput && re_pass === true;

    //      ,          .
    if (!isValidEmail) {
      alert("   .");
    } else if (!isValidInput) {
      alert("   .");
    } else if (!isValidPassword) {
      alert("  1 ,  8   .");
    } else if (!re_pass) {
      alert("  .");
    }
    if (!nickname) {
      console.log("  ");
      const email_nick = email.split("@")[0];
      setInputs({
        ...inputs,
        nickname: email_nick,
      });
    }
    //     
    if (getIsActive) {
      // signUp(inputs);
      axios({
        url: "https://j7c103.p.ssafy.io:8080/api/auth/user/signUp",
        method: "post",
        data: inputs,
      })
        .then((res) => {
          const response = res.data.data;
          // console.log(response);
          alert(res.data.message);
          // const accessToken = response.accessToken;
          // const refreshToken = response.refreshToken;
          // console.log(accessToken);
          // console.log(refreshToken);
          // setRefreshToken(refreshToken);
          // dispatch(SET_TOKEN(accessToken));
          navigate("/");
        })
        .catch((err) => {
          console.log(err);
        });
    }
  };

  return (
    <div className="user-box">
      <div className="login-title">
        <img className="login-logo" src={logo} alt="" />
        <div className="title-box">
          <p className="title-top"></p>
          <p className="title-mid"> </p>
        </div>
      </div>
      <div className="login-page">
        <div className="login-page-name">
          <p></p>
        </div>
        <form className="login-inputs" action="" method="">
          <div className="login-input">
            <p className="login-input-name">
              <span className="color-red"> *</span>
            </p>
            <input
              className="login-input-text"
              autoFocus
              type="email"
              required
              placeholder=" "
              onChange={onChange}
              name="email"
            />
          </div>
          <div className="login-input">
            <p className="login-input-name">
              <span className="color-red"> *</span>
            </p>
            <input
              className="login-input-text"
              type="password"
              required
              placeholder="(, ,  8 )"
              onChange={onChange}
              name="password"
            />
          </div>
          <div className="login-input">
            <p className="login-input-name">
               <span className="color-red"> *</span>
            </p>
            <input
              className="login-input-text"
              type="password"
              required
              placeholder=" "
              onChange={onChangeRePassword}
              name="re_password"
            />
          </div>
          <div className="login-input">
            <p className="login-input-name"></p>
            <input
              className="login-input-text"
              type="text"
              placeholder=" ."
              onChange={onChange}
              name="nickname"
            />
          </div>
          <div className="display-flex">
            <div className="login-input first">
              <p className="login-input-name"></p>
              <input
                className="login-input-text"
                type="number"
                placeholder=" ."
                onChange={onChange}
                name="age"
              />
            </div>
            <div className="login-input">
              <FormControl>
                <p className="login-input-name"></p>
                <RadioGroup
                  row
                  name="gender"
                  onChange={onChange}
                  defaultValue="female"
                >
                  <FormControlLabel
                    value="FEMALE"
                    control={<Radio />}
                    label=""
                  />
                  <FormControlLabel
                    value="MALE"
                    control={<Radio />}
                    label=""
                  />
                </RadioGroup>
              </FormControl>
              {/* <p className="login-input-name"></p>
              <input
                className="login-input-text"
                type="text"
                placeholder="MALE  |  FEMALE"
                onChange={onChange}
                name="gender"
              /> */}
            </div>
          </div>
          <button
            type="submit"
            // className={'login-buttonsss' + (getIsActive ? 'signUpButtonAction' : 'signUpButtonInaction')}
            className="login-button"
            onClick={handleSubmit}
          >
            Signup
          </button>
        </form>
        <div className="signup-link">
          <p className="signup-text">
              ?{" "}
            <Link className="signup-btn" to="/Login">
              
            </Link>
            
          </p>
        </div>
      </div>
      <div className="bottomBack"></div>
    </div>
  );
};
export default Signup;
